import * as React from 'react';
import {Layout, Tabs, Table ,Button, Form, Input,Radio} from 'antd';
import "./invoice.scss";
const TabPane = Tabs.TabPane;
const { Content } = Layout;
// const RadioGroup = Radio.Group;

const columns = [
  { title: '', dataIndex: 'name', key: 'name', },
  { title: '', dataIndex: 'age', key: 'age' },
  { title: '', dataIndex: 'address', key: 'address' },
  { title: '', dataIndex: '', key: 'x', render: () => <a href="javascript:;">Delete</a>,},
  { title: '', dataIndex: 'nature', key: 'nature' },
  { title: '', dataIndex: 'status', key: 'status' }
];
